import React from 'react';
import ContentLoader from "react-content-loader";
import InfiniteScroll from 'react-infinite-scroller';
import { switchLoader } from '../../commonRedux/';
import A from '../../components/A';
import Button from '../../components/Button';
import SocialMenu from '../../components/Menu/SocialMenu';
import { profilePic } from '../../globalFunctions';
import { connectSocket, friendRequest } from '../../hooks/socket';
import { acceptRequest, cancelRequest, followUser, getSuggestions } from '../../http/http-calls';

require ("./styles.scss");

class AllSuggested extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            page:1,
            limit: 20,
            users: [],
            seemore:false,
            showSkeleton: true
        };
    } 
    getSuggestedFriends = (page = this.state.page, key = '') => {
        
        getSuggestions({ limit: this.state.limit, page: page, key:key }, true)
        .then(async resp => {
            console.log("respresp")
            console.log(resp.message);
            this.setState({ users: page == 1 ? resp.message : [...this.state.users, ...resp.message], showSkeleton: false }, () => {
                this.checkMore(1);
            });
        }, error => {
            
        });
    
    }
    
    checkMore = (i) => {
        getSuggestions({ limit: this.state.limit, page: this.state.page+1 }, true)
        .then(async resp => {
           if(resp.message.length > 0){
               this.setState({
                   seemore: true
               })
           }else{
            this.setState({
                seemore: false
            })
           }
        }, error => {
            
        });
    }
    componentDidMount() {
        this.getSuggestedFriends();
        if(this.props.currentUser != undefined){
            connectSocket(this.props.currentUser._id);
        }
        friendRequest((request) => {
            window.location.reload();
        });
    }

    search = (e) => {
        var val = e.target.value;
          this.getSuggestedFriends(1, val);
      };

    addFriend = (id, userIndex) => {
        switchLoader(true, 'Following...! ');
        followUser({ followerid: id }, true)
          .then(async resp => {
            switchLoader();
            let tempUser = this.state.users;
            tempUser[userIndex]._id = resp._id;
            tempUser[userIndex].request = resp.request;
            tempUser[userIndex].type = "sent";
            tempUser[userIndex].uid = resp.followerid;
            if(this.props.refresh != null){
                this.props.refresh();
            }
            this.setState({
                users: tempUser
            })
          }, error => {
            switchLoader();
            
          });
      }

    //   callPendingRequests = () => {
    //     getPendingRequests({}, true)
    //       .then(async resp => {
    //         resp.length == 0 && setShowSkeletonPR(false) ;
    //       }, error => {
    //         switchLoader();
            
    //       });
    //   }
    
    //   callSuggestions = () => {
    //     getSuggestions({ limit: 10, page: 1 }, true)
    //       .then(async resp => {
    //         resp.message.length == 0 && setShowSkeleton(false);
    //         switchLoader();
    //       }, error => {
    //         switchLoader();
            
    //       });
    //   }
    

    
      accept = (user, index, item) => {
        switchLoader(true, 'Following...! ');
        if(item == 0){
            acceptRequest({ id: user._id }, true)
            .then(async resp => {
            switchLoader();
            let tempUser = this.state.users;
            delete tempUser[index];
            if(this.props.refresh != null){
                this.props.refresh();
            }
            this.setState({
                users: tempUser
            })
          }, error => {
            switchLoader();
          });
        }else{
            cancelRequest({id: user.uid}, true)
            .then(async resp => {
              switchLoader();
              let tempUser = this.state.users;
              // tempUser[index] = null;
              delete tempUser[index].request;
              if(this.props.refresh != null){
                  this.props.refresh();
              }
              this.setState({
                  users: tempUser
              })
            }, error => {
              switchLoader();
            });
        }
      }
    
      cancelRequest = (id,index, item) => {
        switchLoader(true, 'Cancelling...! ');
        cancelRequest({id}, true)
          .then(async resp => {
            switchLoader();
            let tempUser = this.state.users;
            // tempUser[index] = null;
            delete tempUser[index].request;
            if(this.props.refresh != null){
                this.props.refresh();
            }
            this.setState({
                users: tempUser
            })
          }, error => {
            switchLoader();
          });
      }
      loadFunc = () => {
        if (!this.state.showSkeleton) {
            this.setState({ 
                showSkeleton: true ,
                page: this.state.page+1
            }, () => {
                this.getSuggestedFriends();
            })
        }

    }

    render() {
        return (
            <div className="seeAllPage">
                <div className="container my-wall-container ">
                <div className="row mt-2">
                    <div className="col-sm empty-container-with-out-border left-column">
                    <SocialMenu  {...this.props} />
                    </div>
                    <div className="col-sm empty-container-with-out-border center-column">
                        <div className="empty-container-with-border w-100 friendsWidget">
                            <h3 className="title">Make New Friends</h3>
                            <form className="w-100">
                                                    
                                                    <input type="text" className="form-control" autoFocus placeholder="Search" id="friendSearch" onChange={this.search} />
                                                    
                                                </form>          
                            {
                            this.state.users.length === 0 && this.state.toggleSkeleton && <p className="no-found">No Data Found</p> 
                            }
                            <InfiniteScroll
                                pageStart={0}
                                initialLoad={false}
                                loadMore={this.loadFunc}
                                hasMore={this.state.seemore}
                            >
                                {this.state.users.map((user, i) => (
                                <ul className="list-group w-100" key={i}>
                                <li className="list-group-item">
                                    <div className="media">
                                        <div className="media-left">
                                            <A href={"/u/"+((user.request && user.request == 'pending') ? user.uid : user._id)}>
                                                <img className="media-object circle widgetAvatar" src={profilePic(user.avatar)}
                                                    alt="..." />
                                            </A>
                                        </div>
                                        <div className="media-body">
                                        <A href={"/u/"+((user.request && user.request == 'pending') ? user.uid : user._id)}><p className="media-heading">{user.name}</p></A>
                                            {/* <p className="media-subheading">{user.job == null ? "Eclipton User" : user.job}</p> */}
                                        </div>
                                        <div className="media-right">
                                            {
                                                user.request == undefined && <Button variant="primary-outline" size="compact"  onClick={() => this.addFriend(user._id, i)}>Add</Button>
                                            }
                                            {
                                                user.request == 'pending' && user.type == "sent" &&  <Button variant="dark" size="compact" layout="dropdown" dropdownOptions={["Cancel"]} onClick={(n) => this.cancelRequest(user.uid, i, n)}>Pending</Button>
                                            }
                                            {
                                                user.request == 'pending' && user.type == "received" && <Button variant="dark" layout="dropdown" dropdownOptions={["Accept", "Reject"]} size="compact" onClick={(n) => this.accept(user, i, n)}>Respond</Button>
                                            }
                                            
                                        </div>

                                    </div>
                                </li>
                            </ul>
                        ))}
                        
                            </InfiniteScroll>
                        

                        {(this.state.showSkeleton) &&
                            <ul className="list-group w-100">
                                <li className="list-group-item">
                                    {Array(3)
                                        .fill()
                                        .map((item, index) => (
                                            <ContentLoader
                                                speed={2}
                                                height={40}
                                                viewBox="0 0 200 40"
                                                backgroundColor="#f3f3f3"
                                                foregroundColor="#ecebeb"
                                                {...this.props}
                                                key={index}
                                            >
                                                <rect x="48" y="8" rx="3" ry="3" width="88" height="6" />
                                                <rect x="48" y="26" rx="3" ry="3" width="52" height="6" />
                                                <circle cx="20" cy="20" r="20" />
                                            </ContentLoader>
                                        ))}
                                </li>
                            </ul>
                        }
                        
                        </div>
                    </div>
                    {/* <!-- end center column --> */}

                    {/* <!--  right column --> */}
                    <div className="col-sm empty-container-with-out-border right-column">
                    {/* <PopularArticles /> */}
                    </div>
                    {/* <!-- end right column --> */}
                </div>
                </div>
            </div>

      )
    }
}

export default AllSuggested;
